@import "./../../index.scss";

.containerLight {
  background-color: #f5f5f5;
  height: 100%;
  overflow: scroll;
}

.containerDark {
  background-color: black;
  height: 100%;
  overflow: scroll;
}

.landingPageContainer {
  display: flex;
  padding: 60px 230px;
  display: flex;
  justify-content: space-between;
  @include maxWidth(1440) {
    padding: 60px 150px;
  }
  @include maxWidth(1080) {
    padding: 60px 90px;
  }
  @include maxWidth(768) {
    padding: 20px 25px;
    flex-wrap: wrap;
    display: block;
  }
  .introTextDark {
    color: white;
    text-align: left;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: -1px;
    font-weight: 700;
    @include maxWidth(768) {
      font-size: 28px;
      line-height: 40px;
      letter-spacing: -1px;
      font-weight: 700;
    }
  }

  .introTextLight {
    color: #42446e;
    text-align: left;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: -1px;
    font-weight: 700;
  }
  .imageContainer {
    display: flex;
    align-items: center;

    img {
      height: 200px;
      width: 200px;
    }
    @include maxWidth(768) {
      margin: 30px auto !important;
      justify-content: center;
    }
  }
  .craft {
    line-height: 50px;
    font-size: 40px;
  }
  .name {
    background: linear-gradient(90deg, #13b0f5 -2.06%, #e70faa 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
}

.consoleContainer {
  min-height: 150px;
}
