@import "./../../index.scss";

.containerLight {
  background-color: #f5f5f5;
  height: 100%;
  overflow: scroll;
  padding: 60px 230px;
  @include maxWidth(768) {
    padding: 0px;
  }
}

.containerDark {
  background-color: black;
  height: 100%;
  overflow: scroll;
  padding: 60px 230px;
  @include maxWidth(768) {
    padding: 0px;
  }
}
